      // console.log(a * b);
      // console.log(NP.round(NP.divide(a,b), 2));
    },
    methods: {
      handleSelect(value) {},
      beginFormat() {
        if (!this.$refs.jsonText.value) {
          return;
        }
        if (!this.isJSON(this.$refs.jsonText.value) || typeof JSON.parse(this.$refs.jsonText.value) !=
          'object') {
          alert('JSON数据格式不正确');
        } else {
          this.jsonData = JSON.parse(this.$refs.jsonText.value);
        }
      },
      arrangeData(data) {
        data['isShowChild'] = false;
        for (let key in data) {
          if (Array.isArray(data[key])) {
            data['isShowChild'] = false;
            this.arrangeData(data[key]);
          } else if (typeof data[key] == 'object' && data[key] !== null) {
            data['isShowChild'] = false;
            this.arrangeData(data[key]);
          }
        }
        return data;
      },
      // 判断变量是否为JSON
      isJSON(str) {
        try {
          JSON.parse(str);
          return true;
        } catch (e) {
          return false;
        }
      }
    }
  }
</script>

