<template>
  <div class="formatItem">
    <div v-for="(item,key) in jsonData">
      <!-- 数组 -->
      <div v-if="Array.isArray(item)">
        <div class="itemTitle" @click="isShowChild(jsonData)" >
          <img src="../assets/elbow-end-minus.gif" />
          <img src="../assets/array.gif" />
          {{ key }}
        </div>
        <div v-if="jsonData.isShowChild" v-show-slide="1" style="padding-left: 15px;" v-for="childItem,index in item" >
          <div class="itemTitle" @click="isShowChild(childItem)"  >
            <img src="../assets/elbow-end-minus.gif" />
            <img src="../assets/object.gif" />
            {{ index }}
          </div>
          <formatItem v-if="childItem.isShowChild" :jsonData='childItem' v-show-slide="childItem.isShowChild"></formatItem>
        </div> 
      </div>
      <!-- 对象 -->
      <div v-else-if="typeof item  == 'object'  && item !== null ">
        <div class="itemTitle" @click="isShowChild(item)" >
          <img src="../assets/elbow-end-minus.gif" />
          <img src="../assets/object.gif" />
          {{ key }}
        </div>
        <formatItem v-if="item.isShowChild" :jsonData='item' v-show-slide="item.isShowChild" ></formatItem>
      </div>
      <div v-else>
        <div v-if=" key != 'isShowChild' ">
          <div class="rowItem" v-if="item == null">
            <img src="../assets/red.gif">
            <div>{{ key }}:null</div>
          </div>
          <div class="rowItem" v-else-if="typeof item == 'string'">
            <img src="../assets/blue.gif">
            <div>{{ key }}: "{{ item }}"</div>
          </div>
          <div class="rowItem" v-else-if="typeof item == 'number'">
            <img src="../assets/green.gif">
            <div>{{ key }}: {{ item }}</div>
          </div>
          <div class="rowItem" v-else>
            <img src="../assets/yellow.gif">
            <div>{{ key }}: {{ item }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from "vue"
  // 展开收起动画
  import VShowSlide from "v-show-slide";
  Vue.use(VShowSlide);
  import formatItem from './formatItem.vue'
  export default {
    name: "formatItem",
    components: {
      formatItem
    },
    props: ['jsonData'],
    methods: {
      isShowChild(data) {
        console.info(data);
        data.isShowChild = !data.isShowChild;
        this.$forceUpdate();
      }
    }
  }
</script>

<style>
  
  /* CSS样式 */
  .fade-enter-active, .fade-leave-active {
    transition: opacity 0.5s; /* 设置过渡效果持续时间 */
  }
   
  .fade-enter, .fade-leave-to {
    height: 0; /* 初始透明度为0，显示为空白 */
  }
  
  .formatItem {
    margin-left: 20px;
  }

  .itemTitle {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  .itemTitle img {
    padding-right: 5px;
  }

  .rowItem {
    display: flex;
    align-items: center;
  }
</style>
