import Vue from 'vue';
import Router from 'vue-router';
import routes from './routes.js';

let originPush = Router.prototype.push;
let originReplace = Router.prototype.replace;

/**
 * 重写push/replace
 * @param {Object} location 告诉原来的push方法，往哪里跳转（传递哪些参数）
 * @param {Object} resolve 成功的回调
 * @param {Object} reject 失败的回调
 * call/apply区别
 * 相同点：都可以调用函数一次，都可以篡改函数的上下文(this）一次
 * 不同点：call/apply传递参数：call传递参数用逗号隔开，apply方法执行，传递数组
 */
Router.prototype.push = function(location, resolve, reject) {
  if (resolve && reject) {
    originPush.call(this, location, resolve, reject);
  } else {
    originPush.call(this, location, () => {}, () => {});
  }
}
Router.prototype.replace = function(location, resolve, reject) {
  if (resolve && reject) {
    originReplace.call(this, location, resolve, reject);
  } else {
    originReplace.call(this, location, () => {}, () => {});
  }
}

// import baseUtils from "../utils/baseUtils.js";

Vue.use(Router);

const router = new Router({
  // mode: 'history', // 不显示#号
  // 保存滚动条位置
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return {
        x: 0,
        y: to.meta.savedPosition || 0
      }
    }
  },
  routes
});

