import Vue from 'vue'
import App from './App.vue'
import router from './router';

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import store from './store/index.js'

import i18n from './i18n/index.js'


// 加载到缓存
Vue.prototype.$saveKeepAliveCache = function() {
  if (!this.$store.state.keepAliveList.includes(this.$route.name)) {
    this.$store.state.keepAliveList.push(this.$route.name);
  }
}

// 清空指定页面缓存
Vue.prototype.$clearKeepAliveCache = function(pageName) {
  const index = this.$store.state.keepAliveList.indexOf(pageName);
  if (index > -1) {
    this.$store.state.keepAliveList.splice(index, 1);
  }
}

// 清空所有缓存
Vue.prototype.$clearKeepAliveAll = function() {
  this.$store.state.keepAliveList = [];
}

Vue.config.productionTip = false

new Vue({
  i18n,
  router,
  store,
  render: h => h(App),
}).$mount('#app')
